.skills {
  min-height: 100vh;
  background-color: #12232E;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h2 {
    margin: 1em;
  }


  .skills__window {
    min-width: 300px;
    max-width: 700px;
    min-height: 300px;
    // max-height: 100%;
    background-color: #EEFBFB;
    background-color: #203647;
    // position: relative;
    border-radius: 8px;
    margin-bottom: 4em;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12);
    
    .window__header {
      display: flex;
      height: 35px;
      width: 100%;      
      background-color: #2d5f99;
      // position: absolute;
      // top: 0;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      

      .window_control-panel {
        display: flex;
        align-self: center;
        
        div {
          width: 12px;
          height: 12px;
          display: block;
          border-radius: 50%;
          margin-left: 0.5em;
        }

        div:nth-of-type(1) {
          background-color: #FC4647;
          margin-left: 1em;
        }
        div:nth-of-type(2) {
          background-color: #FBB025;
        }
        div:nth-of-type(3) {
          background-color: #2AC131;
        }
      }
    }

    .window__content {
      // margin-top: 1em;
      text-align: left;
      padding: 0.5em;
      line-height: 1.5em;      

      span {
        color: rgba(160, 160, 160, 0.5);
        
      }
    }

  }

}

@media screen and (min-width: 820px) {
  .skills__window {
    width: 500px;
  }
}